.Blogs {
  padding: 130px 0;
}

.Blogs-title-2 {
  margin-bottom: 0px;
}

.Blogs-top-part {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin-bottom: 60px;
}

.Blogs-grid {
  display: -ms-grid;
  display: grid;
  grid-gap: 40px;
  -ms-grid-columns: (minmax(300px, 1fr)) [auto-fit];
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.Blogs-grid .Single-blog .picture-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 5px;
  position: relative;
  height: 450px;
  width: 100%;
  overflow: hidden;
  cursor: pointer;
}

.Blogs-grid .Single-blog .picture-container::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: -webkit-gradient(
    linear,
    left bottom,
    left top,
    from(black),
    to(transparent)
  );
  background: linear-gradient(to top, black, transparent);
  z-index: -2;
  opacity: 0.7;
}

.Blogs-grid .Single-blog .picture-container:hover img {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

.Blogs-grid .Single-blog .picture-container img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: left;
  object-position: left;
  z-index: -3;
  -webkit-transition: -webkit-transform 0.7s;
  transition: -webkit-transform 0.7s;
  transition: transform 0.7s;
  transition: transform 0.7s, -webkit-transform 0.7s;
}

.Blogs-grid .Single-blog .picture-container .blog-date {
  background: white;
  text-align: center;
  padding: 10px;
  width: 200px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.Blogs-grid .Single-blog .picture-container .bottom {
  color: white;
  text-align: center;
  text-transform: uppercase;
  padding: 25px;
}

.Blogs-grid .Single-blog .picture-container .bottom .blog-category {
  letter-spacing: 0.5px;
  font-weight: 500;
  font-size: 0.8rem;
}

.Blogs-grid .Single-blog .picture-container .bottom .blog-title {
  font-weight: 600;
  font-size: 1.2rem;
  letter-spacing: 1px;
}

.Blogs-grid .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 25px;
}

@media (max-width: 600px) {
  .Blogs-top-part {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
  }
  .Blogs-top-part .title-1 {
    margin-bottom: 20px;
  }
}
