  }
}

.Showcase {
  overflow: hidden;
  height: calc(100vh - 400px);
  position: relative;
  background-attachment: fixed;
}

.Showcase img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.Showcase .content {
  text-align: center;
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  transform: translate(-50%, -50%);
}

.Showcase .content .Showcase-title {
  font-size: 3.5rem;
  letter-spacing: 1.5px;
  text-shadow: 0px 10px 10px #0000008f;
}

.Showcase .content .Showcase-title-2 {
  font-size: 1.3rem;
  text-shadow: 0px 10px 10px #0000008f;
}

