@import url('https://fonts.googleapis.com/css?family=Oswald:200,400&display=swap');
@import url('https://fonts.googleapis.com/css?family=Playfair+Display&display=swap');
.container {
  margin: 0 auto;
  width: 80%;
  max-width: 1200px;
}

.lg-container {
  margin: 0 auto;
  width: 80%;
  max-width: 1300px;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif;
  outline: none;
}

* a {
  text-decoration: none;
}

.title-1 {
  font-size: 2.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  line-height: 2.9rem;
}

.red-paragraph {
  text-transform: uppercase;
  color: #ef3e36;
  font-weight: 700;
  font-size: 0.8rem;
  letter-spacing: 0.5px;
  margin-bottom: 15px;
}

.card-title {
  color: #ef3e36;
  letter-spacing: 1px;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 30px;
}

.card-text {
  font-size: 1rem;
  letter-spacing: 0.5px;
  text-align: center;
  margin-bottom: 30px;
  color: #818187;
  font-family: 'Playfair Display', serif;
}

.card-link a {
  font-size: 0.9rem;
  text-transform: uppercase;
  color: #b5b5b9;
}

.grey-text {
  margin-top: 30px;
  color: #818187;
  font-weight: 300;
  font-family: 'Playfair Display', serif;
  line-height: 1.7rem;
}

.primary-btn {
  border-style: none;
  border-radius: 3px;
  padding: 15px 25px;
  text-transform: uppercase;
  font-size: 13px;
  color: white;
  background: #ef3e36;
  cursor: pointer;
  letter-spacing: 0.5px;
  -webkit-transition: background 0.3s ease-in;
  transition: background 0.3s ease-in;
}

.primary-btn:hover {
  background: #c91810;
}

.primary-btn:hover {
  background: #c91810;
}

@media (max-width: 600px) {
  .OurWork-grid-bottom {
    padding: 80px 0;
    grid-gap: 30px;
    grid-template-areas: 'box-1' 'box-2' 'box-3' 'box-4';
    -ms-grid-columns: repeat 1fr;
    grid-template-columns: repeat 1fr;
  }
}

.Services {
  padding-bottom: 100px;
}

.Services-grid {
  padding: 100px 0;
  width: 90%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.Services-grid .single-service {
  max-width: 270px;
}

.Services-grid .single-service img {
  height: 45px;
  margin-bottom: 3px;
}

.Services-grid .single-service h1 {
  text-transform: uppercase;
  font-size: 1rem;
  letter-spacing: 1px;
}

.Services-grid .single-service h2 {
  margin-top: 10px;
  color: #818187;
  font-weight: 100;
  font-family: 'Playfair Display', serif;
  font-size: 1rem;
  line-height: 1.5rem;
}

.center-services {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}

.center-services button {
  margin-top: 40px;
}

@media (max-width: 600px) {
  .Services-grid {
    -ms-grid-columns: (minmax(200px, 1fr)) [auto-fit];
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  .Services-grid .single-service {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
  }
  .Services-grid .single-service h1 {
    margin-top: 5px;
    text-align: center;
  }
}
